@media screen and (max-width: 768px) {

  .about {
    padding: 0 0 60px;
  }

  .container-about-me {
    flex-direction: column-reverse;
    justify-content: center;
    padding: 40px 20px;
    min-height: 100vh;
  }

  .container-about-me h3 {
    font-size: 1em;
  }

  .container-about-me p {
    font-size: 0.8em;
  }

  .container-about-me img {
    width: 200px;
    height: 200px;
    margin-bottom: 20px;
  }

  .about-me {
    animation: toRight 1s;
    cursor: default;
    padding: 20px;
    width: 100%;
    margin-top: 20px;
  }
}
